.tab {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .tab button {
    margin-right: 10px;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 16px;
    color: #333;
    padding: 10px;
  }
  
  .dropdown-content {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }
  
  .dropdown-content button {
    display: block;
    width: 100%;
    text-align: left;
    padding: 10px;
    background-color: #f9f9f9;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .dropdown-content button:hover {
    background-color: #ddd;
  }
  
  .active {
    background-color: #333;
    color: #fff;
  }
/* VLCRepositoryMining.css */

/* Styling for the repository-mining container */
.repository-mining {
    margin: 20px;
  }
  
  /* Styling for the h2 heading */
  .repository-mining h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  /* Styling for the notes section */
  .repository-mining .notes {
    margin-bottom: 10px;
  }
  
  /* Styling for the notes list */
  .repository-mining .notes ul {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  /* Styling for individual list items */
  .repository-mining .notes li {
    margin-bottom: 5px;
  }
  
  /* Styling for the iframe elements */
  .repository-mining iframe {
    width: 95vw;
    height: 450px;
    margin-bottom: 20px;
  }
  
